:root {
  --primary-color: #12076f;  /* Blue color */
  --secondary-color: #2ecc71;  /* Green color */
  --text-color: #333;  /* Dark text color */
}


[class*="Panel-"] {
}

.Panel-1-Nav {
  height: 100vh;
  background-color: rgb(255, 255, 255);
  margin: 1%;
  border-radius: 8px;
  width: 30%; /* 30% of the container's width */
  font-size: 30px;
  box-shadow: var(--primary-color) 0 0 0 3px, transparent 0 0 0 0;
}

.Panel-1-Nav > *:first-child {
  padding: 5% 10% 5% 10%;
}

.Panel-2-Nav-User {
  margin: 5%;
  width: 50%;
  border-radius: 8px;
  background-color: rgb(167, 167, 167);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-2-Nav-Tile {
  margin: 5%;
  width: 100%;
  padding: 5%;
  border-radius: 8px;
  background-color: rgb(223, 223, 223);
  box-shadow: 0 0 5px 2px rgba(0,0,0,.35);
}

.Panel-0 {
  display:flex;
  background-color: rgb(255, 255, 255);
  height: 100%;
  width: 100%;
}

ul {
  list-style-type: none;  /* Removes bullet points */
  padding-left: 0;         /* Removes the default padding */
}

.links-1 ul {
  list-style-type: none;  /* Removes bullet points */
  padding-left: 10%;         /* Removes the default padding */
}

.links-1 a {
	color: var(--primary-color);
  position: relative;
  text-decoration: none;
}

.links-1 a::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 4px;
  border-radius: 4px;
  background-color: var(--primary-color);
  bottom: 0;
  left: 0;
  transform-origin: right;
  transform: scaleX(0);
  transition: transform .3s ease-in-out;
}

.links-1 a:hover::before {
  transform-origin: left;
  transform: scaleX(1);
}

.Panel-2-Nav {
  width: 100%;
}

.chat-tile {
  margin: 5%;
  width: 90%;
  padding: 5%;
  align-items: center;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: transparent 0 0 0 3px,rgba(18, 18, 18, .1) 0 6px 20px;
  box-sizing: border-box;
  color: var(--primary-color);
  cursor: pointer;
  display: inline-flex;
  flex: column;
  font-family: Inter,sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  justify-content: center;
  line-height: 1;
  /*margin: 0;*/
  outline: none;
  /*padding: 1rem 1.2rem;*/
  text-align: center;
  text-decoration: none;
  transition: box-shadow .2s,-webkit-box-shadow .2s;
  white-space: nowrap;
  border: 0;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.chat-tile:hover {
  box-shadow: var(--primary-color) 0 0 0 3px, transparent 0 0 0 0;
}

.Panel-1-Chat {
  height: 100vh;
  background-color: rgb(255, 255, 255);
  border-radius: 8px;
  margin: 1%;
  box-shadow: var(--primary-color) 0 0 0 3px, transparent 0 0 0 0;
  flex-grow: 1; /* This makes .Panel-1-Chat take the remaining space */
}

.Panel-2-Send {
  width:86%;
  height:6%;
  background-color: rgb(255, 255, 255);
  margin: 5%;
  padding: 2%;
  border-radius: 8px;
  box-shadow: var(--primary-color) 0 0 0 3px, transparent 0 0 0 0;
}

.Panel-2-Messages {
  width:86%;
  height:70%;
  background-color: rgb(255, 255, 255);
  margin: 5%;
  padding: 2%;
  border-radius: 8px;
  box-shadow: var(--primary-color) 0 0 0 3px, transparent 0 0 0 0;
}

.send-form {
  padding: 1%;
  display: flex;
  flex-direction: row;
}

.send-form-block-message {
  padding:1%;
  width:10%;
}

.send-form-block-input {
  padding:1%;
}

.send-form-block-button {
  padding:1%;
}

.send-form-input {
  appearance: none;
  background-color: none;
  border: 3px solid var(--primary-color);
  line-height: 0;
  font-size: 17px;
  width: 100%;
  display: block;
  box-sizing: border-box;
  padding: 3%;
  border-radius: 60px;
  color: var(--primary-color);
  font-weight: 100;
  letter-spacing: 0.01em;
  position: relative;
  z-index: 1;
}

.send-form-input:focus {
  outline: none;
  background: #ff4a56;
  color: white;
  margin-left: 30px;
}

input[type="text"] {
  -moz-transition: all 0.25s cubic-bezier(0.53, 0.01, 0.35, 1.5);
  -o-transition: all 0.25s cubic-bezier(0.53, 0.01, 0.35, 1.5);
  -webkit-transition: all 0.25s cubic-bezier(0.53, 0.01, 0.35, 1.5);
  transition: all 0.25s cubic-bezier(0.53, 0.01, 0.35, 1.5);
}